import React from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

import Modal from 'react-modal';

import './main.scss'


class Header extends React.Component {
  constructor(props) {
        super(props);
        this.state = {
          isOpen: false
        };
    }

  render() {
    return(
      <div className="header">
        <div className="header__content">
          <h1 style={{ margin: 0 }}>
            <Link to="/" className="header__title">
              Blog of superoo7
            </Link>
          </h1>
            <div>
            <button type="button" onClick={()=>this.setState({isOpen: true})} className="button button--secondary" id="about"><p>about</p></button>
            <br />

            <Modal
                isOpen={this.state.isOpen}
                contentLabel="Information"
                onRequestClose={()=>this.setState({isOpen: false})}
                className="boxed-view__box"
                overlayClassName="boxed-view boxed-view--modal"
            >
               <h2>superoo7's Blog</h2>
                <p>
                    I am a front end web developer!
                </p>
                <p>
                    Website build and design by <a href="http://superoo7.com">superoo7</a>
                </p>
                <br />
                <p id="close" onClick={()=>this.setState({isOpen: false})}>
                  Close
                </p>
            </Modal>
            </div>

        </div>
      </div>

    );
  }
}

const TemplateWrapper = ({ children }) =>
  <div>
    <Helmet
      title="Gatsby Default Starter"
      meta={[
        { name: 'description', content: 'Sample' },
        { name: 'keywords', content: 'sample, something' },
      ]}
    />
    <Header />
    <div
      style={{
        margin: '0 auto',
        maxWidth: 960,
        padding: '0px 1.0875rem 1.45rem',
        paddingTop: 0,
      }}
    >
      {children()}
    </div>
  </div>

TemplateWrapper.propTypes = {
  children: PropTypes.func,
}

export default TemplateWrapper



